<template>
  <section class="features">
    <div class="row">
      <h2> {{ headings[0] }}</h2>
      <p class="long-copy"> {{ longCopy[0] }}</p>
    </div>
    <div id="js--wp-1" class="row">
      <div class="col span-1-of-4 box">
        <i class="fa fa-pencil icon-big"></i>
        <h3> {{ headings[1] }}</h3>
        <p> {{ longCopy[1] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-undo icon-big"></i>
        <h3> {{ headings[2] }}</h3>
        <p> {{ longCopy[2] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-etsy icon-big"></i>
        <h3> {{ headings[3] }}</h3>
        <p> {{ longCopy[3] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-code icon-big"></i>
        <h3> {{ headings[4] }}</h3>
        <p> {{ longCopy[4] }}</p>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'
  export default {
    mounted () {
      let wp1 = new Waypoint({
        element: document.getElementById('js--section-skills'),
        offset: '60px',
        handler: () => {
          $('#js--wp-1').addClass('animated fadeInLeft')
        }
      })
    },
    data () {
      return {
        headings: [
          'All My Knowledge \u2014 so far',
          'Front-end',
          'Back-end',
          'English',
          'Coding'
        ],
        longCopy: [
          `Along all the way on my career. I've experienced various frameworks or worked with many languages. I've managed to improve on every one of them in some and not on all of them. So let me show you my forts below.`,
          'I developed interest on front-end development many years ago. I always thought that internet is a great tool if used correctly. So what\'s the best way to demonstrate but with a great user experience in a web page!.',
          'During my 4 years in college. I managed to learn some back-end coding and understand the basics. I\'m sure I can manage to code some neat back-end on the actual year, depending on the language. I haven\'t developed back-end since 2017.',
          'My english is pretty good (i think). I studied english for around 7 years without interruptions, and can understand it pretty well. My weakness in the language, as expected is the pronunciation.',
          'I love my career. There\'s so much to learn about it. I simply love what I do. I love coding and spending hours thinking about a solution whenever a problem shows up. I have experience in some coding languages/frameworks as shown on the images below.'
        ]
      }
    }
  }
</script>



