<template>
  <main>
    <app-header v-show="isSticky"></app-header>
    <sticky-header v-show="!isSticky"></sticky-header>
    <features id="js--section-skills"></features>
    <fav-meals id="js--section-meals"></fav-meals>
    <how-it-works id="js--section-my-plans"></how-it-works>
    <our-cities id="js--section-courses"></our-cities>
    <testimonials id="js--section-about-me"></testimonials>
    <plans id="js--section-resumee"></plans>
    <contact id="js--section-contact"></contact>
    <app-footer></app-footer>
  </main>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import HowItWorks from './sections/Steps.vue'
  import FavMeals from './sections/Meals.vue'
  import Features from './sections/Features.vue'
  import StickyHeader from './sections/StickyNav.vue'
  import Header from './sections/Header.vue'
  import OurCities from './sections/Cities.vue'
  import Testimonials from './sections/Testimonials.vue'
  import Plans from './sections/Plans.vue'
  import Contact from './sections/Contact.vue'
  import Footer from './sections/Footer.vue'

  export default {
    mounted () {
      let self = this
      let mainWp = new Waypoint({
        element: document.getElementById('js--section-skills'),
        offset: '60px',
        handler: function (direction) {
          if (direction === 'down') {
            self.isSticky = false
          } else {
            self.isSticky = true
          }
        }
      })
    },
    data () {
      return {
        isSticky: true
      }
    },
    components: {
      appHeader: Header,
      StickyHeader,
      Features,
      FavMeals,
      HowItWorks,
      OurCities,
      Testimonials,
      Plans,
      Contact,
      appFooter: Footer
    }
  }
</script>



