<template>
  <section class="meals">
    <ul class="meals-showcase clearfix">
      <li class="meals-showcase-list" v-for="meal in meals">
        <figure class="meals-showcase-list-item" :title="meal.description">
          <img
            class="meals-showcase-list-item-photo"
            :src="require('../../assets/images/' + meal.image)"
            :alt="meal.description">
          <div class="meals-showcase-list-item-photo-overlay">
            <p class="meals-showcase-list-item-photo-overlay-text">{{ meal.description }}</p>
          </div>
        </figure>
      </li>
    </ul>
  </section>
</template>
<script>
  export default {
    data () {
      return {
        meals: [
          { image: 'vuejs_logo.jpg', description: 'Learnt by myself. Bought some courses. My best framework by now.' },
          { image: 'html_logo.jpg', description: 'Been learning and using it for the last 3 years.' },
          { image: 'csharp_logo.jpg', description: 'Learn in school by doing a platform at unison. Last time I developed in C# was 1 year ago.' },
          { image: 'javascript_logo.jpg', description: 'Javascript. Been learning it every single day for the last 3 years.' },
          { image: 'css_logo.jpg', description: 'Paleo beef steak with vegetables' }
        ]
      }
    }
  }
</script>



