<template>
  <header>
    <nav class="main-nav sticky">
      <div class="row">
        <img @click="goTo('app')" src="../../assets/images/Ricardo_Rojas.png" alt="main-logo" class="logo-black">
        <transition name="slide">
          <ul v-show="menuIsActive" class="main-nav-options">
            <li v-for="section in sections">
              <span @click="goTo(section.id)">{{ section.title }}</span>
            </li>
          </ul>
        </transition>
        <span @click="toggleMenu"
              class="main-nav-button">
          <i class="icon-mid"
             :class="{'fa fa-bars': !menuIsActive, 'fa fa-times': menuIsActive}"></i>
        </span>
      </div>
    </nav>
  </header>
</template>
<script>
  export default {
    data () {
      return {
        menuIsActive: true,
        sections: [
          {
            title: 'My Skills',
            id: 'js--section-skills'
          },
          {
            title: 'My Plans',
            id: 'js--section-my-plans'
          },
          {
            title: 'Courses',
            id: 'js--section-courses'
          },
          {
            title: 'About Me',
            id: 'js--section-about-me'
          },
          {
            title: 'Resumee',
            id: 'js--section-resumee'
          },
          {
            title: 'Contact',
            id: 'js--section-contact'
          }
        ]
      }
    },
    methods: {
      toggleMenu () {
        let self = this
        self.menuIsActive = !self.menuIsActive
      },
      goTo (sectionId) {
        let self = this
        self.$SmoothScroll(document.getElementById(sectionId), 750)
      }
    }
  }
</script>
<style scoped>
  .slide-enter-active, .slide-leave-active {
    transition: opacity .5s;
  }
  .slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>




