<template>
  <section class="steps">
    <div class="row">
      <h2>My short terms &#8212; Future Plans</h2>
    </div>
    <div class="row">
      <div class="col span-1-of-2 step-box">
        <img id="js--wp-2"
             class="steps-appStore"
             src="../../assets/images/wills.png"
             alt="Omnifood on iPhone">
      </div>
      <div class="col span-1-of-2 step-box">
        <div v-for="step in steps" class="steps-step">
          <div>{{ step.number }}</div>
          <p>{{ step.description }}</p>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'

  export default {
    mounted () {
      let wp2 = new Waypoint({
        element: document.getElementById('js--wp-2'),
        offset: '50%',
        handler: () => {
          $('#js--wp-2').addClass('animated fadeInUp')
        }
      })
    },
    data () {
      return {
        steps: [
          {
            number: 1,
            description: 'The next framework I\'m willing to learn is Angular. Since it\'s one of the most used & demanded frameworks.'
          },
          {
            number: 2,
            description: 'I would love to start learning android. Most of people have a smart phone, so it\'s worth a try learning it.'
          },
          {
            number: 3,
            description: 'We are getting more apple users on the city as well. So swift it\'s becoming an option to compete for a job on the mobile development market.'
          }
        ]
      }
    }
  }
</script>



