<template>
  <section class="cities">
    <div class="row">
      <h2>Courses i've been through &#8212; Most recent</h2>
    </div>
    <div id="js--wp-3" class="row">
      <div class="col span-1-of-3 cities-box" v-for="course in courses">
        <img :src="require(`../../assets/images/${course.image}.jpg`)" :alt="course.title">
        <h3> {{ course.title }}</h3>
        <div class="cities-box-feature">
          <i class="fa fa-book icon-small"></i>
          {{ course.lectures }}
        </div>
        <div class="cities-box-feature">
          <i class="fa fa-clock-o icon-small"></i>
          {{ course.hours }}
        </div>
        <div class="cities-box-feature">
          <i class="fa fa-globe icon-small"></i>
          <a :href="course.src" target="_blank">{{ course.sourceTitle }}</a>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'

  export default {
    mounted () {
      let wp3 = new Waypoint({
        element: document.getElementById('js--section-courses'),
        offset: '20%',
        handler: () => {
          $('#js--wp-3').addClass('animated fadeInUpBig')
        }
      })
    },
    data () {
      return {
        courses: [
          {
            title: 'Vue JS 2 - The Complete Guide. With Vue Router & Vuex',
            image: 'course_1',
            lectures: '375 lectures',
            hours: '21 hours',
            sourceTitle: 'See it on Udemy',
            src: 'https://www.udemy.com/vuejs-2-the-complete-guide/learn/v4/overview'
          },
          {
            title: 'The Ultimate Vue JS 2 Developers Course',
            image: 'course_2',
            lectures: '178 lectures',
            hours: '13 hours',
            sourceTitle: 'See it on Udemy',
            src: 'https://www.udemy.com/vuejs-2-essentials/learn/v4/overview'
          },
          {
            title: 'Real World Websites with HTML5 and CSS3',
            image: 'course_3',
            lectures: '77 lectures',
            hours: '12 hours',
            sourceTitle: 'See it on Udemy',
            src: 'https://www.udemy.com/design-and-develop-a-killer-website-with-html5-and-css3/learn/v4/overview'
          }
        ]
      }
    }
  }
</script>



