<template>
  <div id="app">
    <div v-if="!assetsLoaded">
      <fade-spinner :loading="loading" :color="color" :height="height" :width="width"></fade-spinner>
      Loading assets...
    </div>
    <div v-else>
      <page-content></page-content>
    </div>
  </div>
</template>

<script>
  /* eslint-disable no-unused-vars */

  import fadeSpinner from 'vue-spinner/src/FadeLoader.vue'
  import PageContent from './components/PageContent.vue'
  import CONFIG from './config'
  import { Preloader } from './services/image-preloader-service'

  export default {
    name: 'app',
    created () {
      let self = this
      Preloader.bind(CONFIG.imageSrc, {
        complete: () => {
          let self = this
          self.assetsLoaded = true
        },
        error: () => {
          console.log('Failed to load one or moe images!')
        }
      })
    },
    data () {
      return {
        loading: true,
        color: '#000',
        height: '',
        width: '',
        assetsLoaded: false
      }
    },
    components: {
      fadeSpinner,
      PageContent
    }
  }
</script>



