<template>
  <section class="plans">
    <div class="row">
      <h2>Start eating healthy today</h2>
    </div>
    <div class="row">
      <div v-for="resumeeInfo in resumeeInfo">
        <div class="row">
          <div class="plans-box">
            <div>
              <h3> {{ resumeeInfo.title }}</h3>
              <p class="plans-box-price-meal">{{ resumeeInfo.fullInfo }}</p>
            </div>

            <div>
              <ul class="plans-box-content">
                <li v-for="item in resumeeInfo.content" class="plans-box-content-item">
                  <i class="fa fa-check icon-mini"></i>
                  {{ item }}
                </li>
              </ul>
            </div>

            <div>
              <a target="_blank" :class="resumeeInfo.btn" :href="resumeeInfo.src">Open Resumee</a>
            </div>

          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  export default {
    data () {
      return {
        resumeeInfo: [
          {
            title: 'Check out my resumee',
//            price: `399$ <span>/ month</span>`,
            fullInfo: 'Get to know all my experiences.',
            content: [
              'Know my proyects',
              'Check my laboral experience',
              'Deep-in skills',
              'Know my education/studies'
            ],
            btn: {
              'btn': true,
              'btn-full': true
            },
            src: './../../static/resumee.pdf'
          }
        ]
      }
    }
  }
</script>



