<template>
  <section class="testimonials">
    <div class="row">
      <h2>Let me say some things about myself.</h2>
    </div>
    <div class="row">
      <div v-for="customer in customers" class="col span-1-of-4">
        <blockquote class="testimonials-quote">
          {{ customer.quote }}
        </blockquote>
      </div>
    </div>
  </section>
</template>
<script>
  export default {
    data () {
      return {
        customers: [
          {
            quote: 'I\'m very committed with my job. I\'m always trying to get the job done by the deadline provided. If I get stuck on something, the internet has never let me down on those situations. There\'s plenty sources to get information from.\n',
            image: 'customer-1',
            name: 'Alberto Duncan'
          },
          {
            quote: 'I\'m not afraid to ask. If I need help, I know I can count on teammates with more experience than mine to lay on, and learn from them.\n',
            image: 'customer-2',
            name: 'Joana Silva'
          },
          {
            quote: 'Looking for courses it\'s a great way to learn for me. It lets me learn while not distracting me from my daily duties.\n',
            image: 'customer-3',
            name: 'Milton Chapman'
          },
          {
            quote: 'I know how to learn from my mistakes. I like, when falling into mistakes, being corrected by someone. That gives me an opportunity to keep growing in knowledge and not falling in the same errors.\n',
            image: 'customer-3',
            name: 'Milton Chapman'
          }
        ]
      }
    }
  }
</script>

<!--
Omnifood is just awesome! I just launched a startup which leaves me with no time for cooking, so Omnifood is a life-saver. Now that I got used to it, I couldn't live without my daily meals!
(Alberto Duncan)

Inexpensive, healthy and great-tasting meals, delivered right to my home. We have lots of food delivery here in Lisbon, but no one comes even close to Omifood. Me and my family are so in love!
(Joana Silva)

I was looking for a quick and easy food delivery service in San Franciso. I tried a lot of them and ended up with Omnifood. Best food delivery service in the Bay Area. Keep up the great work!
(Milton Chapman)

-->



