<template>
  <header>
    <nav class="main-nav">
      <div class="row">
        <img src="../../assets/images/Ricardo_Rojas.png" alt="main-logo" class="logo">
        <transition name="slide">
          <ul v-show="menuIsActive" class="main-nav-options">
            <li v-for="section in sections">
              <span @click="goTo(section.id)">{{ section.title }}</span>
            </li>
          </ul>
        </transition>
        <span
          @click="toggleMenu"
          class="main-nav-button">
          <i class="icon-mid"
             :class="{'fa fa-bars': !menuIsActive, 'fa fa-times': menuIsActive}">
          </i>
        </span>
      </div>
    </nav>
    <div class="hero-text-box">
      <h1>Hello!. My name is Ricardo Rojas Leon.<br/>Welcome to my page.</h1>
      <span @click="goTo('js--section-about-me')" class="btn btn-full">About Me</span>
      <span @click="goTo('js--section-skills')" class="btn btn-ghost">My Skills</span>
    </div>
  </header>
</template>
<script>
  export default {
    data () {
      return {
        longCopy: 'I am a person that likes to learn new things. Open to new experiences and always interested on knowing about new technologies.',
        menuIsActive: true,
        sections: [
          {
            title: 'My Plans',
            id: 'js--section-my-plans'
          },
          {
            title: 'Courses',
            id: 'js--section-courses'
          },
          {
            title: 'Resumee',
            id: 'js--section-resumee'
          },
          {
            title: 'Contact',
            id: 'js--section-contact'
          }
        ]
      }
    },
    methods: {
      toggleMenu () {
        let self = this
        self.menuIsActive = !self.menuIsActive
      },
      goTo (sectionId) {
        let self = this
        self.$SmoothScroll(document.getElementById(sectionId), 750)
      }
    }
  }
</script>
<style scoped>
  .slide-enter-active, .slide-leave-active {
    transition: opacity .5s;
  }
  .slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



